.container {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.navBar {
  overflow: hidden;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 40px;
  background-color: rgba(51, 51, 51, 0.9);
}

.content {
  display: flex;
  width: 100%;
  height: calc(100% - 40px);
  
  justify-content: space-between;
}

.contentWrapper {
  display: flex;
  flex: 1;
}

@media screen and (max-width: 640px) {
  .navBar {
    height: 80px;
  }
  .content {
    height: calc(100% - 80px);
  }
}